.content2 {
  overflow: unset;
}
.room-main {
  padding: 13.4375em 0 6.25em 0;
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 2.1875em;
  }
  &__slider {
    width: calc(100% - 50.4375em);
    &-btns {
      position: absolute;
      z-index: 2;
      right: 0;
      bottom: 0;
      max-width: 12.875em;
      display: flex;
      align-items: center;
      border-radius: 3.125em 0;
      opacity: 0.8899999856948853;
      background: #FFF;
      padding: 0.875em 1.875em;
    }
  }
  &__desc {
    width: 46.6875em;
    margin-left: 3.75em;
  }
  .gallery {
    position: sticky;
    top: 0.625em;
    &__big {
      .swiper-slide {
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s;
        &-active {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    &__small {
      margin-top: 1.25em;
      .swiper-slide {
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s;
        &-active,
        &-next,
        &-next + .swiper-slide {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
  }
  &__slide {
    border-radius: 3.125em;
    img {
      width: 100%;
      border-radius: 3.125em;
    }
  }
  .swiper-button-prev {
    position: static;
    margin-top: 0;
  }
  .swiper-button-next {
    position: static;
    margin-top: 0;
    margin-left: 0.5em;
  }
  &__title {
    font-family: Caveat;
    font-size: 4.0625em;
    line-height: 120%;
    font-weight: 400;
    color: #ffffff;
  }
  &__info {
    border-radius: 3.125em;
    background: radial-gradient(48.21% 34.38% at 98.06% 61.58%, rgba(255, 176, 103, 0.50) 0%, rgba(255, 176, 103, 0.00) 100%), #FFF;
    box-shadow: 0 0 1.5625em 0 #FFF;
    padding: 2.8125em;
    margin-top: 2.75em;
    &-top {
      padding-bottom: 2.75em;
      position: relative;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
  }
  &__item {
    display: flex;
    align-items: center;
    &-icon {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 3.75em;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-text {
      display: flex;
      align-items: center;
      font-size: 1.3125em;
      line-height: 120%;
      color: #2B231B;
      margin-left: 0.875em;
      span {
        font-weight: 700;
        margin-left: 1.25em;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 1;
    margin-top: 1.25em;
  }
  &__btn {
    width: 15.625em;
    height: 3.125em;
  }
  &__btn2 {
    width: 15.625em;
    height: 3.125em;
    margin-left: 1.875em;
  }
  &__img {
    position: absolute;
    right: -5em;
    bottom: 0;
    width: 18.75em;
  }
  &__line {
    position: absolute;
    left: -2.8125em;
    bottom: 0;
    width: calc(100% + 5.625em);
    height: 0.1875em;
  }
  &__info-bottom {
    padding-top: 2.1875em;
  }
  &__info-text {
    p {
      font-size: 0.9375em;
      line-height: 140%;
      color: #2B231B;
    }
  }
}
.tabs {
  margin-top: 3.4375em;
  &__head {
    display: flex;
    align-items: flex-end;
    min-height: 7em;
  }
  &__body {
    border-radius: 0 0 3.125em 3.125em;
    background: #FFF;
    box-shadow: 0 0 1.5625em 0 #FFF;
    padding: 2.8125em 3.3125em 3.3125em 3.3125em;
  }
  .tab {
    font-size: 1.3125em;
    line-height: 120%;
    color: #2B231B;
    width: 20.0625em;
    height: 4.375em;
    display: flex;
    flex-grow: 1;
    justify-content: center;
    align-items: center;
    border-radius: 3.125em 3.125em 0 0;
    border: 1px solid #FFF;
    background: rgba(255, 255, 255, 0.01);
    box-shadow: 0 0 1.5625em 0 #FFF;
    cursor: pointer;
    transition: all 0.5s;
    &--active {
      height: 5.3125em;
      background: #ffffff;
    }
    &:hover {
      background: #ffffff;
    }
  }
  &__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2em * 1 / 2));
    gap: 2em;
    li {
      display: flex;
      align-items: center;
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label:after {
          background: #AE8E71;
        }
      }
      label {
        font-size: 1.125em;
        line-height: 140%;
        font-weight: 300;
        color: #2B231B;
        padding-left: 1.5625em;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 0.625em;
          height: 0.625em;
          border-radius: 50%;
          background: #FFF;
          box-shadow: 0 0 0.625em 0 #C9AA8D;
        }
      }
    }
  }
}


@media screen and (max-width: 992px) {
  .room-main {
    padding: 10.625em 0 4.375em;
    &__content {
      margin-top: 1.25em;
    }
    &__slider {
      width: calc(100% - 32.4375em);
    }
    &__desc {
      width: 31.8125em;
      margin-left: 0.625em;
    }
    &__title {
      font-size: 2.5em;
    }
    &__info {
      padding: 1.5625em 1.25em;
      margin-top: 0;
      &-top {
        padding-bottom: 1.5625em;
      }
      &-bottom {
        padding-top: 1.25em;
      }
    }
    &__item {
      &-icon {
        width: 3.125em;
      }
      &-text {
        font-size: 1.0625em;
        span {
          margin-left: 0.625em;
        }
      }
    }
    &__btns {
      display: block;
    }
    &__btn2 {
      margin: 0.9375em 0 0 0;
    }
    &__img {
      right: 0em;
      width: 12em;
    }
    &__line {
      left: -1.25em;
      width: calc(100% + 2.5em);
    }
    &__info-text {
      p {
        font-size: 0.875em;
      }
    }
  }
  .tabs {
    margin-top: 1.5625em;
    .tab {
      width: 15em;
    }
  }
}

@media screen and (max-width: 768px) {
  .room-main {
    &__content {
      display: block;
    }
    &__slider {
      width: 100%;
    }
    &__desc {
      width: 100%;
      margin: 2.5em 0 0 0;
    }
    &__title {
      font-size: 2.5em;
    }
    &__info {
      margin-top: 1.25em;
    }
  }
  .tabs {
    &__body {
      padding: 3.75em 2.5em;
    }
    .tab {
      font-size: 1em;
      width: 11.6875em;
    }
    &__list {
      grid-template-columns: repeat(2, calc(50% - 1.25em * 1 / 2));
      gap: 1.25em;
      li {
        label {
          font-size: 0.875em;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .tabs {
    &__body {
      padding: 2em 1.25em;
    }
    .tab {
      width: 8.625em;
    }
    &__list {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 450px) {
  .room-main {
    &__item {
      &-icon {
        width: 2.5em;
      }
      &-text {
        font-size: 0.9375em;
      }
    }
  }
  .tabs {
    .tab {
      font-size: 0.9375em;
      width: 5.9375em;
    }
  }
}