.header {
  position: absolute;
  z-index: 5;
  top: 1.25em;
  left: 0;
  right: 0;
  width: 100%;
  padding-top: 2.5em;
  padding-left: 9.25em;
  &2 {
    left: 9.25em;
    right: 1.1875em;
    width: auto;
    background-image: url("../img/header-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: 0 0 1.5625em 0 #FFF;
    border-radius: 3.125em;
    padding: 2.5em 0;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(90deg, #FFF 0%, rgba(255, 255, 255, 0.00) 100%);
      border-radius: 3.125em;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 2;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__center {
    margin-left: 1em;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-left: 1em;
  }
  &__logo {
    flex-shrink: 0;
    display: block;
    width: 6em;
    img {
      width: 100%;
    }
  }
  &__info {
    max-width: 7.5em;
    margin-left: 1.0625em;
    span {
      font-size: 0.875em;
      line-height: 120%;
      font-weight: 300;
      color: #574839;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      &:not(:first-child) {
        margin-left: 2em;
      }
    }
  }
  &__link {
    font-family: Caveat;
    font-size: 1.25em;
    line-height: 120%;
    color: #574839;
    text-transform: uppercase;
    &:hover {
      color: #AE8E71;
    }
  }
  &__item {
    position: relative;
    &--active {
      .header__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      display: flex;
      align-items: center;
      flex-direction: column;
      cursor: pointer;
      position: relative;
      &:hover {
        span {
          color: #AE8E71;
        }
      }
      span {
        font-family: Caveat;
        font-size: 1.25em;
        line-height: 120%;
        color: #574839;
        text-transform: uppercase;
        transition: all 0.5s;
      }
      i {
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translate(-50%, 0);
        width: 0.875em;
        img {
          width: 100%;
        }
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      padding-top: 0.875em;
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
    }
    &-content {
      min-width: 10em;
      background: #ffffff;
      border-radius: 1.5625em;
      box-shadow: 0px 0px 25px 0px #FFF;
      padding: 1.5625em;
    }
    &-link {
      display: block;
      font-size: 0.9375em;
      line-height: 120%;
      color: #2B231B;
      &:not(:first-child) {
        margin-top: 0.625em;
      }
      &:hover {
        color: #AE8E71;
      }
    }
  }
  &__tel {
    &:hover {
      span {
        color: #AE8E71;
      }
    }
    span {
      font-family: Caveat;
      font-size: 1.75em;
      line-height: 120%;
      font-weight: 700;
      color: #574839;
      transition: all 0.5s;
    }
  }
  &__btn {
    width: 14.0625em;
    height: 3.125em;
    margin-left: 2em;
  }
  &__burger {
    display: none;
  }
}

@media screen and (max-width: 1800px) {
  .header {
    &__right {
      flex-direction: column;
    }
    &__btn {
      margin: 0.625em 0 0 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    top: 0.375em;
    padding-top: 2em;
    padding-left: 0;
    &2 {
      left: 0;
      right: 0;
      padding-bottom: 2em;
    }
    &__right {
      margin-left: 2em;
    }
    &__list {
      li {
        &:not(:first-child) {
          margin-left: 1.375em;
        }
      }
    }
    &__link {
      font-size: 0.875em;
    }
    &__item {
      &-show {
        span {
          font-size: 0.875em;
        }
      }
    }
    &__tel {
      span {
        font-size: 1.25em;
      }
    }
    &__btn {
      width: 11.25em;
      height: 2.5em;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__center {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100%;
      background: #ffffff;
      padding: 6.25em 1.875em 1.875em;
      margin-left: 0;
      transition: all 0.5s;
      &--active {
        left: 0;
      }
    }
    &__right {
      flex-direction: row;
    }
    &__list {
      flex-direction: column;
      li {
        &:not(:first-child) {
          margin: 1em 0 0 0;
        }
      }
    }
    &__item {
      &--active {
        .header__item-hidden {
          display: block;
        }
      }
      &-hidden {
        position: static;
        opacity: 1;
        pointer-events: all;
        display: none;
      }
      &-content {
        min-width: 100%;
        padding: 0;
        box-shadow: unset;
        background: transparent;
        border-radius: unset;
      }
    }
    &__btn {
      margin: 0 0 0 0.9375em;
    }
    &__burger {
      display: flex;
      width: 2.5em;
      height: 2.5em;
      padding: 0.875em 0.5625em;
      background: #AE8E71;
      border-radius: 50%;
      margin-left: 0.9375em;
      position: relative;
      z-index: 10;
      cursor: pointer;
      span {
        position: absolute;
        left: 0.5625em;
        width: calc(100% - 1.125em);
        height: 0.0625em;
        background: #ffffff;
        transition: all 0.5s;
        &:nth-child(1) {
          top: 0.875em;
        }
        &:nth-child(2) {
          top: 50%;
          transform: translate(0, -50%);
        }
        &:nth-child(3) {
          bottom: 0.875em;
        }
      }
      &--active {
        span {
          &:nth-child(1) {
            top: 1.25em;
            transform: rotate(-45deg);
          }
          &:nth-child(2) {
            display: none;
          }
          &:nth-child(3) {
            bottom: 1.1875em;
            transform: rotate(45deg);
          }
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__info {
      display: none;
    }
  }
}

@media screen and (max-width: 451px) {
  .header {
    &__tel {
      display: none;
    }
  }
}