.content {
  padding: 1.25em 0 1.6875em 8.0625em;
  overflow: hidden;
  position: relative;
}
.sidebar {
  position: fixed;
  z-index: 5;
  top: 1.25em;
  left: 0;
  width: 8.0625em;
  height: calc(100vh - 2.9375em);
  border-radius: 0 3.125em 3.125em 0;
  background: #FDFEFD;
  box-shadow: 0 0 1.25em 0 #FFF;
  padding-top: 3.125em;
  &--active {
    z-index: 4;
  }
  .container {
    padding: 0 0.625em;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
  }
  &__item {
    position: relative;
    &--active {
      .sidebar__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      width: 2.25em;
      position: relative;
      z-index: 1;
      img {
        width: 100%;
      }
    }
    &-hidden {
      position: absolute;
      left: 100%;
      top: 50%;
      transform: translate(0, -50%);
      width: max-content;
      height: 4.375em;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1.25em;
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: -3.125em;
        width: calc(100% + 3.125em);
        height: 100%;
        background: #FFF;
        box-shadow: 0 0 1.25em 0 rgba(199, 199, 199, 0.25);
        border-radius: 0 3.125em 3.125em 0;
      }
      a {
        position: relative;
        z-index: 1;
        font-size: 0.9375em;
        line-height: 120%;
        font-weight: 300;
        color: #000000;
        &:hover {
          color: #AE8E71;
        }
      }
    }
  }
  &__img {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 12em;
    img {
      width: 100%;
    }
  }
}
.main {
  padding-right: 1.6875em;
  padding-left: 1.1875em;
  position: relative;
  &__img {
    position: absolute;
    z-index: 3;
    right: 1.6875em;
    bottom: 0;
    width: 38.4375em;
  }
  &__img2 {
    position: absolute;
    z-index: 3;
    right: 0;
    bottom: -3.125em;
    width: 10em;
  }
  &__slider {
    position: relative;
  }
  &__swiper {
    overflow: unset;
  }
  &__slide {
    border-radius: 3.125em;
    padding: 16.25em 0;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      z-index: 2;
      top: 18.75em;
      right: -1.875em;
      width: 64.375em;
      height: 64.375em;
      background: radial-gradient(50% 50.00% at 50% 50.00%, #FFB067 0%, rgba(217, 217, 217, 0.00) 100%, rgba(255, 176, 103, 0.00) 100%);
      border-radius: 50%;
      pointer-events: none;
    }
    &-bg {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      box-shadow: 0 0 1.5625em 0 #FFF;
      border-radius: 3.125em;
    }
  }
  &__desc {
    max-width: 53em;
    position: relative;
    z-index: 4;
  }
  &__title {
    font-family: Caveat;
    font-size: 5em;
    line-height: 120%;
    color: #AE8E71;
  }
  &__text {
    max-width: 39.0625em;
    margin-top: 0.9375em;
    p {
      font-size: 1.625em;
      line-height: 140%;
      font-weight: 100;
      color: #574839;
    }
  }
  &__btn {
    width: 14.375em;
    height: 3.125em;
    margin-top: 1.5625em;
  }
  &__slider-nav {
    position: relative;
  }
  &__slider-btns {
    position: absolute;
    left: 16.5em;
    bottom: 16.25em;
    height: 3.125em;
    display: flex;
    align-items: center;
  }
  .swiper-button-prev {
    position: static;
    margin-top: 0;
  }
  .swiper-button-next {
    position: static;
    margin-top: 0;
    margin-left: 0.625em;
  }
}
.advantages {
  padding: 0 0 5em 0;
  margin-top: -3.125em;
  position: relative;
  z-index: 3;
  &__slider {
    position: relative;
  }
  &__swiper {
    overflow: unset;
  }
  @media screen and (min-width: 577px) {
    &__swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4) - (1.875em * 3 / 4)));
      gap: 1.875em;
      transform: unset !important;
    }
    .swiper-slide {
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
}
.advantage-card {
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: linear-gradient(180deg, #FFF 0%, #F2E4D7 100%);
  box-shadow: 0 0 1.5625em 0 #FFF;
  border-radius: 3.125em;
  padding: 1.875em 0.625em 0.9375em;
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5.125em;
    margin: 0 auto 0.9375em;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title {
    max-width: 16.875em;
    margin: auto auto 0 auto;
    span {
      font-family: Caveat;
      font-size: 1.625em;
      line-height: 120%;
      color: #AE8E71;
    }
  }
}
.rooms {
  background-image: url("../img/rooms-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 2.5em 0 5em;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 9.0625em;
    background: linear-gradient(180deg, #C9AA8D 0%, rgba(201, 170, 141, 0.00) 100%);
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__top {
    display: flex;
    justify-content: space-between;
  }
  &__slider {
    position: relative;
    margin-top: 2.1875em;
    &-btns {
      display: flex;
      align-items: center;
    }
  }
  &__swiper {
    overflow: unset;
    &-wrapper {
      align-items: center;
    }
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
    &-active,
    &-prev,
    &-next {
      opacity: 1;
      pointer-events: all;
    }
    &-active {
      .room-card__img {
        width: calc(100% + 5em);
        margin-left: -2.5em;
      }
    }
  }
  .swiper-button-prev {
    position: static;
    margin-top: 0;
  }
  .swiper-button-next {
    position: static;
    margin-top: 0;
    margin-left: 0.5em;
  }
}
.room-card {
  &__img {
    border-radius: 3.125em;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      transform: rotate(-180deg);
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, #FFF 0%, rgba(255, 255, 255, 0.00) 47.40%);
      border-radius: 3.125em;
    }
    img {
      width: 100%;
      box-shadow: 0 0 1.5625em 0 #FFF;
      border-radius: 3.125em;
    }
  }
  &__desc {
    margin-top: 3.125em;
  }
  &__info {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 0.625em * 1 / 2 ));
    gap: 0.625em;
    background: #FFFFFF;
    box-shadow: 0 0 1.5625em 0 #FFF;
    border-radius: 3.125em;
    padding: 0.5em;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 0.0625em;
      height: calc(100% - 1em);
      background: #AE8E71;
      opacity: 0.2;
    }
  }
  &__quantity {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    span:nth-child(1) {
      font-family: Caveat;
      font-size: 3.375em;
      line-height: 80%;
      color: #AE8E71;
    }
    span:nth-child(2) {
      font-size: 1.125em;
      line-height: 120%;
      color: #000000;
      margin-left: 0.9375em;
    }
  }
  &__size {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    span:nth-child(1) {
      font-family: Caveat;
      font-size: 3.375em;
      line-height: 80%;
      color: #AE8E71;
    }
    span:nth-child(2) {
      font-size: 1.125em;
      line-height: 120%;
      color: #000000;
      margin-left: 0.9375em;
    }
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    margin-top: 1.5em;
  }
  &__text {
    p {
      font-family: Caveat;
      font-size: 2.25em;
      line-height: 120%;
      color: #ffffff;
    }
  }
  &__btn {
    width: 10.625em;
    height: 3.125em;
    margin-left: 1em;
  }
  &__best {
    position: absolute;
    z-index: 1;
    left: 1.5625em;
    bottom: 1.25em;
    span {
      font-family: Caveat;
      font-size: 1.5625em;
      line-height: 120%;
