      &__img {
        padding-bottom: 75%;
      }
    }
  }
  .offer-card {
    &__img {
      width: 11.875em;
    }
    &__title {
      padding: 0.5em 1em 0.5em 1.5625em;
      margin-left: -0.625em;
      span {
        font-size: 0.875em;
      }
    }
    &__bg {
      width: 10.625em;
    }
  }
  .info {
    &__content {
      padding: 11.5625em 1.25em 3.125em 3.125em;
      margin-top: 3.125em;
    }
    &__img {
      width: 10.375em;
      top: -1.5625em;
      left: 50%;
      right: unset;
      transform: translate(-50%, 0);
    }
    &__list {
      li {
        margin-left: 0 !important;
      }
    }
  }
  .contacts {
    padding: 0 0 28.125em 0;
    .container {
      padding: 0;
    }
    &__content {
      width: 100%;
      padding-bottom: 4.375em;
    }
  }
}

@media screen and (max-width: 399px) {
  .advantages {
    .swiper-slide {
      opacity: 0;
      pointer-events: none;
      &-active {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
}