.display2 {
  font-family: Caveat;
  font-size: 3.25em;
  line-height: 120%;
  color: #ffffff;
}
.text30 {
  span {
    font-family: Caveat;
    font-size: 1.875em;
    line-height: 140%;
    color: #574839;
  }
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-brown {
  background-image: url("../img/btn-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: 0px 0px 5px 0px #FFB067;
  border-radius: 3.125em;
  &:hover {
    background: #ffffff;
    span {
      color: #2B231B;
    }
  }
  span {
    font-size: 1em;
    line-height: 120%;
    font-weight: 300;
    color: #FFFFFF;
  }
}
.btn-white {
  background-image: url("../img/btn-bg2.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: 0px 0px 5px 0px #FFB067;
  border-radius: 3.125em;
  &:hover {
    background-image: url("../img/btn-bg.webp");
    span {
      color: #ffffff;
    }
  }
  span {
    font-size: 1em;
    line-height: 120%;
    font-weight: 300;
    color: #574839;
  }
}
.swiper-button-prev,
.swiper-button-next {
  z-index: 1;
  width: 100%;
  &:after {
    display: none;
  }
  img {
    width: 100%;
  }
}
.breadcrumbs {
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    display: flex;
    align-items: center;
    font-size: 1em;
    line-height: 120%;
    font-weight: 300;
    color: #ffffff;
    margin-bottom: 1em;
    &:not(:last-child) {
      margin-right: 0.0625em;
    }
    &--active {
      color: #D0D0D0;
    }
  }
}
.up {
  position: fixed;
  z-index: 7;
  right: 1.25em;
  bottom: 1.25em;
  width: 4.375em;
  height: 4.375em;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #574839;
  box-shadow: 0 0 1.5625em 0 #FFF;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s;
  &:hover {
    background: #AE8E71;
  }
  &--active {
    opacity: 1;
    pointer-events: all;
  }
  img {
    width: 1.125em;
    height: auto;
  }
}
.nav {
  padding: 3.125em 0;
  &__list {
    li {
      font-size: 1.25em;
      line-height: 120%;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 1em;
      }
      &:hover {
        opacity: 0.8;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .btn-brown {
    span {
      font-size: 0.875em;
    }
  }
  .btn-white {
    span {
      font-size: 0.875em;
    }
  }
}

@media screen and (max-width: 768px) {
  .display2 {
    font-size: 2.1875em;
  }
  .text30 {
    span {
      font-size: 1.5625em;
    }
  }
}