.footer {
  border-radius: 0 0 3.125em 3.125em;
  background: #FFF;
  box-shadow: 0 0 1.5625em 0 #FFF;
  padding: 2.5em 0;
  &__inner {
    display: flex;
  }
  &__logo {
    span {
      font-size: 1.375em;
      line-height: 120%;
      color: #2B231B;
    }
  }
  &__desc {
    display: flex;
    align-items: start;
    margin-left: 8.125em;
  }
  &__nav {
    &-title {
      span {
        font-family: Caveat;
        font-size: 1.625em;
        line-height: 120%;
        color: #574839;
      }
    }
  }
  &__list {
    margin-top: 0.9375em;
    li {
      font-size: 0.875em;
      line-height: 130%;
      color: #2B231B;
      position: relative;
      padding-left: 1em;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        width: 0.3125em;
        height: 0.3125em;
        background: #2B231B;
      }
    }
  }
  &__flex {
    display: flex;
    align-items: flex-end;
    margin-left: 4.125em;
    .footer__nav:not(:first-child) {
      margin-left: 4.125em;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__inner {
      display: block;
    }
    &__desc {
      margin: 1.5625em 0 0 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 2.5em 0 4.375em;
    &__desc {
      display: block;
    }
    &__flex {
      margin: 1.5625em 0 0 0;
    }
  }
}

@media screen and (max-width: 450px) {
  .footer {
    &__flex {
      display: block;
      .footer__nav {
        margin: 1.25em 0 0 0 !important;
      }
    }
  }
}